.header{
    background-color: whitesmoke;
    color: #0047AB;
    text-align: center;
    width: 100%;
    margin: 0;
    padding: 10px;
    position:fixed;
    top:0;
    z-index:1000;
    display: flex;
    align-items: center;
    justify-content: space-between;
}


.header_settings{
    font-size: large;
}

.hidden_fields{
    display: None;
}

.header_h1{
    display: flex;
}

.agent_header{
    padding-right: 20px;
    font-size: large;
}


.llama_icon{
   width: 50px;
   height: 50px;
   padding-right: 5px;
}

.chat-container{
    background-color: white;
    color: #0047AB;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100%;
    padding: 10px;
}

.chat_template{
    margin-top: 60px;
    height: 70%;
    width: 50%;
    overflow-y: auto;
    padding: 10px;
}

.message-input-container{
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    position: fixed;
    bottom: 10px;
    padding-right: 10px;
}

.submit_form{
    padding: auto;
    display: flex;
    margin-left: 50px;
    margin-right: 50px;
    border-radius: 10px;
    width: 80%;
    justify-content: space-evenly;
    height: 100px;
}   



.input{
    width: 60%;
    padding: 5px;
    color: #0047AB;
    border-radius: 15px;
    border: none;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: large;
    background-color: whitesmoke;
}

.file{
    color: #0047AB;
    margin-left: auto;
    background-color: whitesmoke;
    border: none;
    padding-right: 20px;
    font-size: medium;
    font-weight: bold;
}

.button{
    background-color: whitesmoke;
    border: none;
    color: black;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 12px;
}